.page {
  padding: var(--space-6) 0 var(--space-6);
}

.header {
  margin-bottom: var(--space-4);
}

.header h1 {
  font-size: clamp(2rem, 4vw, 2.8rem);
  margin-bottom: 0.5rem;
}

.header p {
  color: var(--muted);
}

.form {
  display: grid;
  gap: var(--space-3);
}

.grid {
  display: grid;
  gap: var(--space-2);
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.fieldLabel {
  display: grid;
  gap: 0.4rem;
  font-weight: 500;
  color: var(--text);
}

.fieldInput,
.fieldTextarea {
  padding: 0.65rem 0.8rem;
  border-radius: 10px;
  border: 1px solid var(--accent);
  background: #ffffff;
}

.fieldTextarea {
  min-height: 90px;
  resize: vertical;
}

.fullWidth {
  grid-column: 1 / -1;
}

.checkboxRow {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  font-weight: 500;
  grid-column: 1 / -1;
}

.checkboxInput {
  width: 16px;
  height: 16px;
}

.success {
  padding: 0.75rem 1rem;
  border-radius: 12px;
  background: #eef6f0;
  color: #1f4d2c;
  border: 1px solid #cde7d4;
}

.error {
  padding: 0.75rem 1rem;
  border-radius: 12px;
  background: #fcecec;
  color: #7a1f1f;
  border: 1px solid #f2c7c7;
}
